#drop-files {
  position:relative;
  width: 500px;
  height: 140px;
  background: rgba(0,0,0,0.1);
  border-radius: 10px;
  border: 4px dashed rgba(0,0,0,0.2);
  padding-top:80px;
  text-align: center;
  font-size: 2em;
  font-weight: bold;
}

#drop-files p {
  clear:none;
  padding:0;
  margin:0;
}

#uploaded-holder {
  display: none;
  position:relative;
  margin: 0 auto;
}

#dropped-files {
  display:block;
  width: 650px;
}

#upload-button {
  z-index: 9999;
  display: none;
  margin: 20px 0;
}

.drop-button {
  display: block;
  position: absolute;
  z-index: 9999;
  padding: 5px;
  width: 100%;
  background: rgba(0,0,0,0.6);
  font-size: 1em;
  bottom: 0;
  text-align: center;
  text-decoration: none;
  font-weight: 700;
  color: #FFF;
  cursor: pointer;
}

.drop-button:hover{
  text-decoration: underline;
  color: red;
}

#dropped-files .image {
  position: relative;
  height: 133px;
  width: 200px;
  border: 4px solid #fff;
  box-shadow: 0px 0px 10px rgba(0,0,0,0.1);
  background: #fff;
  float: left;
  border-radius: 4px;
  margin: 0 7px 7px 0;
  overflow: hidden;
}
